#opcv-paper-container {
  height: var(--r-paper-height);
  width: var(--r-paper-width);
  background-color: white;
}
#opcv-editor-container {
  margin-left: 8mm;
  background-color: white;
  width: calc(var(--r-paper-width) - 30mm);
  height: var(--r-paper-height);
  border-radius: 5px;
  padding: 5mm;
  box-sizing: border-box;
}
#opcv-editor-container .edit-box {
  height: 100%;
  overflow-y: auto;
}
