#resume0-content {
  line-height: var(--r-line-height);
}
#resume0-content .header {
  height: 5mm;
  background-color: var(--r-theme-color);
}
#resume0-content .main {
  padding: 10mm 10mm 8mm;
  font-size: var(--r-small-font-size);
  line-height: var(--r-line-height);
}
#resume0-content .base-info {
  display: flex;
  justify-content: space-between;
  max-height: 35mm;
}
#resume0-content .base-info a {
  text-decoration: none;
}
#resume0-content .school-logo {
  height: 13mm;
  width: 25mm;
}
#resume0-content .personal-info {
  flex: 1 1;
  display: flex;
  align-items: center;
  flex-direction: column;
  font-size: var(--r-large-font-size);
  box-sizing: border-box;
  margin-bottom: 3mm;
}
#resume0-content .personal-info .name {
  font-size: var(--r-large-font-size);
  font-weight: bold;
  margin-bottom: calc(var(--r-line-margin) + 1mm);
}
#resume0-content .personal-info .more {
  font-size: var(--r-small-font-size);
}
#resume0-content .photo {
  height: 32mm;
  margin-bottom: 3mm;
  width: 25mm;
  font-size: var(--r-large-font-size);
  overflow: hidden;
  display: flex;
  align-items: center;
}
#resume0-content .photo .photo-block {
  background-color: var(--r-theme-color);
  width: 100%;
  height: 100%;
  display: flex;
  font-size: var(--r-small-font-size);
  align-items: center;
  justify-content: center;
  color: white;
}
#resume0-content .line-title0 {
  display: flex;
  align-items: center;
  font-size: var(--r-medium-font-size);
  font-weight: bold;
  color: var(--r-theme-color);
  border-left: 2mm solid var(--r-theme-color);
  padding: 0 4mm;
  background-color: var(--r-border-color);
  margin: calc(var(--r-line-margin) * 2) 0 var(--r-line-margin);
  box-sizing: border-box;
  height: calc(var(--r-line-height) + 2mm);
}
#resume0-content .line-title1 {
  display: flex;
  align-items: center;
  font-size: var(--r-medium-font-size);
  font-weight: bold;
  color: var(--r-theme-color);
  border-bottom: 1px solid var(--r-theme-color);
  margin: calc(var(--r-line-margin) * 2) 0 var(--r-line-margin);
  box-sizing: border-box;
  height: calc(var(--r-line-height) + 2mm);
}
#resume0-content .line-title2 {
  display: flex;
  align-items: end;
  font-size: var(--r-medium-font-size);
  font-weight: bold;
  color: var(--r-theme-color);
  margin: calc(var(--r-line-margin) * 2) 0 var(--r-line-margin);
  box-sizing: border-box;
  height: calc(var(--r-line-height) + 2mm);
}
#resume0-content .line-title2 .title-main {
  background-color: var(--r-theme-color);
  color: white;
  height: 100%;
  display: flex;
  padding: 0 4mm;
  align-items: center;
}
#resume0-content .line-title2 .title-hr {
  flex: 1 1;
  height: 2px;
  background-color: var(--r-border-color);
}
#resume0-content .line-title3 {
  display: flex;
  align-items: center;
  font-size: var(--r-medium-font-size);
  font-weight: bold;
  color: var(--r-theme-color);
  margin: calc(var(--r-line-margin) * 2) 0 var(--r-line-margin);
  box-sizing: border-box;
  height: calc(var(--r-line-height) + 2mm);
}
#resume0-content .line-title3 .title-hr {
  flex: 1 1;
  height: 2px;
  background-color: var(--r-border-color);
  margin-left: 4mm;
}
#resume0-content .line-two-sides {
  display: flex;
  justify-content: space-between;
  margin-bottom: var(--r-line-margin);
}
#resume0-content .bold-text {
  font-weight: bold;
}
