#resume0-editor {
  min-height: 2000px;
}
#resume0-editor .general-info-editor {
  background-color: #F7FAFF;
  border-radius: 8px;
  padding: 3mm;
  margin-bottom: 5mm;
}
#resume0-editor .general-info-editor .btn-containers {
  display: flex;
}
#resume0-editor .general-info-editor .btn-containers button {
  margin-left: 3mm;
}
#resume0-editor .general-info-editor .info-title {
  font-size: var(--r-large-font-size);
  font-weight: bold;
  margin-bottom: 3mm;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#resume0-editor .general-info-editor .info-form .config-form-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 5mm;
}
#resume0-editor .general-info-editor .info-form .form-item {
  display: flex;
  align-items: center;
  margin-top: 5mm;
}
#resume0-editor .general-info-editor .info-form .form-item .item-name {
  width: 80px;
}
#resume0-editor .general-info-editor .general-info-tips {
  line-height: calc(var(--r-line-height) + 0.5mm);
  margin-top: 3mm;
  font-size: var(--r-small-font-size);
  color: #a9a9a9;
}
#resume0-editor .line-item {
  display: flex;
  align-items: center;
  margin-top: 5mm;
}
#resume0-editor .tool-box {
  display: flex;
  margin-bottom: 20px;
  justify-content: space-between;
}
